* {
  box-sizing: border-box;
}

body {
  background-color: #5b6c6e;
  font-family: Arial, sans-serif;
}

img {
  max-width: 100%;
}

.container {
  max-width: 448px;
  margin: 0 auto;
  padding: 0 15px;
}

.js-span {
  justify-content: center;
  margin: 10px auto;
  font-size: 18px;
  display: flex;
}

.gallery {
  border-radius: 8px;
  flex-wrap: wrap;
  justify-content: center;
  gap: 12px;
  padding: 12px;
  transition: box-shadow .3s;
  display: flex;
}

.photo-card {
  border-radius: 8px;
  flex-basis: calc(50% - 6px);
  transition: box-shadow .3s, transform .3s;
  overflow: hidden;
  box-shadow: 0 4px 8px #0000001a;
}

.photo-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 16px #0003;
}

.photo-card a {
  border-bottom: 1px solid #ddd;
  display: block;
  overflow: hidden;
}

.photo-card a img {
  border-radius: 8px 8px 0 0;
  width: 100%;
  height: auto;
  transition: transform .3s;
  display: block;
}

.photo-card a:hover img {
  transform: scale(1.1);
}

.search-form {
  justify-content: center;
  margin: 0 auto 16px;
  display: flex;
}

input {
  width: 100%;
  padding: 8px;
}

.info {
  color: #ddd;
  border-radius: 0 0 8px 8px;
  gap: 8px;
  padding: 8px;
  display: flex;
}

.info p:hover {
  color: #9fbddc;
}

@media screen and (min-width: 668px) {
  .photo-card {
    flex-basis: calc(50% - 6px);
  }
}

@media screen and (min-width: 1158px) {
  .container {
    max-width: 1158px;
  }

  .photo-card {
    flex-basis: calc(33.3333% - 8px);
  }
}

